import { Component, OnInit, Input, Output, ElementRef, EventEmitter, AfterViewInit, AfterContentInit } from '@angular/core';
import { Library } from '../library.model';
import { Widget } from '../widget.model';

@Component({
  selector: 'app-widgets-library',
  template: `<div class='title'>Library</div>
<ul class='list-group'>
  <li *ngFor='let libraryWidget of library.widgets' class='list-group-item'>
    <span>{{libraryWidget.widget.title}}</span>
    <!-- <span *ngIf='libraryWidget.isRendered; else not' class='material-icons' (click)='onRemoveWidget(libraryWidget.widget)'>remove_circle_outline</span>
    <ng-template #not>
      <span class='material-icons' (click)='onAddWidget(libraryWidget.widget)'>add_circle_outline</span>
    </ng-template> -->
    <!-- <div class='pretty p-switch p-fill'>
      <input id='libraryWidget.widget.id' type='checkbox' [checked]='libraryWidget.isRendered' (change)='onCheckboxChange(libraryWidget)' />
    </div> -->
    <span class='pretty p-switch p-fill'>
      <input type='checkbox' [checked]='libraryWidget.isRendered' (change)='onCheckboxChange(libraryWidget)' />
      <div class='state p-success'>
        <label></label>
      </div>
    </span>
  </li>
</ul>
<div class='button-container'>
  <button class='btn btn-primary' (click)='onDoneClick()'>Done</button>
</div>`,
  styles: [`:host{position:absolute;z-index:100;background:#fff}ul{list-style:none;padding:0;margin-bottom:20px}ul>li{overflow:hidden;text-overflow:ellipsis}ul>li>span:first-of-type{float:left;margin-right:30px}ul>li>span:last-of-type{float:right;margin-top:5px}label:after{margin-top:-1px}.pretty input{width:200%}`]
})
export class LibraryComponent implements OnInit, AfterContentInit, AfterViewInit {
  @Input() library: Library;
  @Output() done = new EventEmitter<void>();
  @Output() renderWidget = new EventEmitter<Widget>();
  @Output() derenderWidget = new EventEmitter<Widget>();

  constructor(private elementRef: ElementRef) { }

  ngOnInit() { }

  ngAfterContentInit() {
    this.elementRef.nativeElement.classList.add('card', 'card-row', 'popup');
  }

  ngAfterViewInit() {
    const width = this.elementRef.nativeElement
      .getBoundingClientRect()
      .width;
    this.elementRef.nativeElement.style.top = (this.library.yPosition - 20) + 'px';
    this.elementRef.nativeElement.style.left = (this.library.xPosition - width + 20) + 'px';
  }

  onDoneClick(): void {
    this.done.emit();
  }

  onCheckboxChange(libraryWidget: any) {
    if (libraryWidget.isRendered) {
      this.library.derenderWidget(libraryWidget.widget);
      this.derenderWidget.emit(libraryWidget.widget);
    } else {
      this.library.renderWidget(libraryWidget.widget);
      this.renderWidget.emit(libraryWidget.widget);
    }
  }
}
