import { Component, OnInit, Input, Output, EventEmitter, ElementRef, AfterViewInit, AfterContentInit } from '@angular/core';
import { LayoutEditor } from '../layout-editor.model';

@Component({
  selector: 'app-widgets-layout-editor',
  template: `<div class='title'>Layout Editor</div>
<p>
  <b>Coming Soon!</b>
</p>
<p>
  This will allow some users to edit pages and make custom layouts targeted for specific roles in your organization.
</p>
<p>
  For now, changes made will not be saved.
</p>
<div class='button-container'>
  <button class='btn btn-primary' (click)='onDoneClick()'>Done</button>
</div>`,
  styles: [`:host{position:absolute;z-index:100;background:#fff}`]
})
export class LayoutEditorComponent implements OnInit, AfterContentInit, AfterViewInit {
  @Input() layoutEditor: LayoutEditor;
  @Output() done = new EventEmitter<void>();

  constructor(private elementRef: ElementRef) { }

  ngOnInit() { }

  ngAfterContentInit() {
    this.elementRef.nativeElement.classList.add('card', 'card-row', 'col-8', 'col-sm-6', 'popup');
  }

  ngAfterViewInit() {
    const width = this.elementRef.nativeElement
      .getBoundingClientRect()
      .width;
    this.elementRef.nativeElement.style.top = (this.layoutEditor.yPosition - 20) + 'px';
    this.elementRef.nativeElement.style.left = (this.layoutEditor.xPosition - width + 20) + 'px';
  }

  onDoneClick(): void {
    this.done.emit();
  }
}
