import { Component, OnInit, ElementRef, Input, Output, EventEmitter, AfterContentInit } from '@angular/core';

@Component({
  selector: 'app-widgets-panel-spacer',
  template: `<div (mouseenter)='onMouseEnter()' (touchenter)='onTouchEnter()' (mouseleave)='onMouseLeave()' (touchleave)='onTouchLeave()'>
</div>`,
  styles: [`:host{height:30px}:host>div{height:100%}`]
})
export class SpacerComponent implements OnInit, AfterContentInit {
  @Input() spacer: object;
  @Output() spacerHovered = new EventEmitter<SpacerComponent>();
  @Output() spacerUnhovered = new EventEmitter<void>();

  constructor(private elementRef: ElementRef) { }

  ngOnInit() { }

  ngAfterContentInit() {
    this.elementRef.nativeElement.classList.add('col-12');
  }

  onMouseEnter() {
    this.spacerHovered.emit(this);
  }

  onTouchEnter() {
    this.spacerHovered.emit(this);
  }

  onMouseLeave() {
    this.spacerUnhovered.emit();
  }

  onTouchLeave() {
    this.spacerUnhovered.emit();
  }
}
